"use strict";

import ajax from './ajax';

function getCountry(url, cb) {
    ajax(url, function(response) {
        try {
            const res = JSON.parse(response);
            cb(res || null);
        } catch (e) {
            cb(null);
        }
    });
}


export default getCountry


