"use strict";

import { h, Component } from 'preact';
import ajax from '../../lib/ajax';
import Logger from '../../log';

class DeferredCall extends Component {

    constructor(props){
        super(props);
        this.log = this.props.modal.props.lang.forLog;
        this.setState({number: ""});
        Logger.setLog(props.work ? this.log.openDefWorkTime : this.log.openDefNotWorkTime);
        this.days = [];
        this.startMinutes = [];
        this.endMinutes = [];
        this.hourIndexesStart = [];
        this.hourIndexesEnd = [];
        this.hourCurrentIndex = 0;
        this.minutesCurrentIndex = 0;
        this.dayIndex = 0;
        this.changeNumber = this.changeNumber.bind(this);
        this.deferredClick = this.deferredClick.bind(this);
        this.changeDaySelect = this.changeDaySelect.bind(this);
        this.changeHourSelect = this.changeHourSelect.bind(this);
        this.changeMinuteSelect = this.changeMinuteSelect.bind(this);
        this.getWorkDays(props);
        this.getHours(0);
        this.getMinutes(0);
    }
    deferredClick (){
        let regex = this.props.modal.numberRegex;
        let isValidate = false;
        if(regex.length>0){
            for(let i = 0; i<regex.length; i++){
                if(regex[i].test(this.state.number)){
                    isValidate = true;
                    break;
                }
            }
        }
        else isValidate = true;
        if(isValidate&&this.state.number!=""){
            Logger.setLog(this.log.orderDefCall +" "+this.state.number);
            let jobject = {};
            let date = new Date(this.days[this.dayIndex][0].date);
            date.setHours(this.state.hours[this.hourCurrentIndex], this.state.minutes[this.minutesCurrentIndex], 0, 0);
            let tmpDate = new Date();
            let tmpDateString = new Date(tmpDate.toLocaleString('en-US', { timeZone: this.props.modal.props.config.calendar.timezone }));
            let tmpDateStringUTC = new Date(tmpDate.toLocaleString('en-US', { timeZone: 'UTC' }));
            let timeOffset = (tmpDateString-tmpDateStringUTC)/60000;
            let clientOffset = date.getTimezoneOffset();
            date.setMinutes(date.getMinutes()+timeOffset+clientOffset);
            jobject.callback_time = date.getTime();
            jobject.href = window.location.href;
            jobject.user_agent = navigator.userAgent;
            jobject.number = this.state.number;
            jobject.location = this.props.modal.props.location;
            jobject.logs = Logger.getData();
            ajax(this.props.modal.props.config.publicPostApi+"?domain="+WebitelCallbackDomain+"&widget="+WebitelCallbackId, (response) => {
                const res = JSON.parse(response);
                if(res.status === "error"){
                    Logger.setLog("Server error: "+res.info);
                    this.setState({
                        errorMessage: res.info
                    })
                }
                else{
                    // Logger.setLog("Замовлення відкладеного дзвінка");
                    this.props.modal.changeViewToShowDeferred(this.days[this.dayIndex][0].stringDate,
                        this.state.hours[this.hourCurrentIndex].toString().replace( /^([0-9])$/, '0$1' )
                        +":"+this.state.minutes[this.minutesCurrentIndex].toString().replace( /^([0-9])$/, '0$1' ), this.props.work);
                }
                console.log(res);
            }, jobject);
        }
        else{
            Logger.setLog(this.log.invalidNumber);
            this.setState({
                invalidNumber: true
            })
        }
    }
    changeNumber (event){
        let number =  event.target.value.replace(/[^0-9]/g,'');
        this.setState({
            number: number,
            invalidNumber: false,
            errorMessage: false
        });
    }
    changeMinuteSelect (event){
        Logger.setLog(this.log.setMinutes + " " + this.state.minutes[event.target.selectedIndex]);
        this.minutesCurrentIndex = event.target.selectedIndex;
    }
    changeDaySelect (event) {
        Logger.setLog(this.log.setDay + " " + this.days[event.target.selectedIndex][0].stringDate);
        this.dayIndex = event.target.selectedIndex;
        this.hourCurrentIndex = 0;
        this.minutesCurrentIndex = 0;
        this.getHours(event.target.selectedIndex);
        this.getMinutes(this.hourCurrentIndex);
    }

    changeHourSelect (event) {
        Logger.setLog(this.log.setHours + " " + this.state.hours[event.target.selectedIndex]);
        this.hourCurrentIndex = event.target.selectedIndex;
        this.minutesCurrentIndex = 0;
        this.getMinutes(event.target.selectedIndex);
    }

    getHours (index){
        this.hourIndexesStart = [];
        this.hourIndexesEnd = [];
        let data = [].concat(this.days[index]);
        data.sort(this.compareMinutes);
        let tmpHours = [];
        let startIndex = 0;
        for(let g = 0; g < data.length; g++){
            this.hourIndexesStart.push(startIndex);
            let startHour = parseInt(data[g].startTime/60);
            let endHour = parseInt(data[g].endTime/60);
            this.startMinutes[g] = data[g].startTime - startHour*60;
            this.endMinutes[g] = data[g].endTime - endHour*60;
            if(this.startMinutes[g] >= 45){
                startHour++;
                this.startMinutes[g] = -1;
            }
            if(this.endMinutes[g] == 0){
                endHour--;
                this.endMinutes[g] = 60;
            }
            for(let i = startHour; i < endHour + 1; i++)
            {
                tmpHours.push(i);
                startIndex++;
            }
            this.hourIndexesEnd.push(startIndex - 1);
        }
        this.setState({hours: tmpHours});
    }

    getMinutes (index){
        let tmp = [0,15,30,45];
        let spec = false;
        for(let i = 0; i < this.startMinutes.length; i++){
            if(index == this.hourIndexesStart[i]){
                spec = true;
                this.setState({ minutes: tmp.filter((item)=>{ return item>this.startMinutes[i]}) });
            }
        }
        for(let i = 0; i < this.endMinutes.length; i++){
            if(index == this.hourIndexesEnd[i]){
                spec = true;
                this.setState({ minutes: tmp.filter((item)=>{ return item<=this.endMinutes[i]}) });
            }
        }
        if(!spec){
            this.setState({ minutes: tmp });
        }
    }

    compareDays(a,b) {
        if (a.weekDay < b.weekDay)
            return -1;
        if (a.weekDay > b.weekDay)
            return 1;
        return 0;
    }

    compareMinutes(a,b) {
        if (a.startTime < b.startTime)
            return -1;
        if (a.startTime > b.startTime)
            return 1;
        return 0;
    }

    getWorkDays(args){
        this.days = [];
        let currDate = new Date();
        let currMinutes = currDate.getHours()*60+currDate.getMinutes();
        let tmpDate = new Date();
        let sortedWeek = args.modal.props.calendarAccept;
        let exceptDays = args.modal.props.config.calendar.except;
        sortedWeek.sort(this.compareDays);
        let dayCount = 0;
        let count = 3;
        let realDays = -1;
        let addDay = false;
        let isExcept = false;
        if(sortedWeek.length<3){count = sortedWeek.length}
        for(let i = currDate.getDay(); i<8; i++)
        {
            isExcept = false;
            realDays++;
            for(let e = 0; e < exceptDays.length; e++){
                let date = exceptDays[e].date.split("-");
                if(exceptDays[e].repeat === 1){
                    if(date[0]==currDate.getMonth()+1&&date[1]==currDate.getDate()+realDays){
                        isExcept = true;
                        break;
                    }
                }
                else{
                    if(date[0]==currDate.getFullYear()&&date[1]==currDate.getMonth()+1&&date[2]==currDate.getDate()+realDays){
                        isExcept = true;
                        break;
                    }
                }
            }
            if(isExcept)continue;
            if(dayCount<count)
            {
                let tmp = sortedWeek.filter((item)=>{ return item.weekDay==i});
                let tmpFirst = [];
                if(tmp.length>0) {
                    if(realDays==0)
                    {
                        for(let t = 0; t < tmp.length; t++)
                        {
                            if(tmp[t].startTime<=currMinutes&&currMinutes<tmp[t].endTime){
                                tmp[t].startTime=currMinutes;
                                tmpFirst.push(tmp[t]);
                                addDay = true;
                            }
                            else if(currMinutes<tmp[t].startTime){
                                tmpFirst.push(tmp[t]);
                                addDay = true;
                            }
                        }
                    }
                    else
                    {
                        addDay = true;
                    }
                    if(addDay){
                        tmpDate.setDate(currDate.getDate()+realDays);
                        if(realDays==0) this.days.push(tmpFirst);
                        else this.days.push(tmp);
                        this.days[dayCount][0].stringDate = tmpDate.getDate().toString().replace( /^([0-9])$/, '0$1' )+'.'+(tmpDate.getMonth()+1).toString().replace( /^([0-9])$/, '0$1' );
                        this.days[dayCount][0].date = new Date(tmpDate);
                        dayCount++;
                    }
                    if(dayCount == 3) break;
                }
            }

            if(dayCount<count && i==7)i=0;
        }

    }
    render(props) {
        let invalidMessage = (<div class="invalid-number" style={props.modal.props.config.css.errorMessage} visible>
            {props.lang.invalidNumberMessage}
        </div>);
        let errorMessage = (<div class="invalid-number" style={props.modal.props.config.css.errorMessage} visible>
            {this.state.errorMessage && this.state.errorMessage.startsWith("-ERR") ? props.lang.serverErrorMessage : this.state.errorMessage}
        </div>);
        return (
            <div>
                <div class="call-me-deferred">{props.work ? props.lang.deferredTitle : props.lang.notWorkTitle}</div>
                <div class="call-me-deferred-text">{props.work ? props.lang.deferredText : props.lang.notWorkText}</div>
                <div class="call-me-deferred-hours">
                    <select class = "minimal" style={props.modal.props.config.css.input} value = {this.dayIndex} onChange={this.changeDaySelect} >
                        {this.days.map( (i, index) => {
                            return <option value={index}  >{i[0].stringDate}</option>
                        })}
                    </select>
                    <select class = "minimal" style={props.modal.props.config.css.input} value = {this.hourCurrentIndex}  onChange={this.changeHourSelect}>
                        {this.state.hours.map((j, index)=>{
                            return <option value={index}  >{j.toString().replace( /^([0-9])$/, '0$1' )}</option>
                        })}
                    </select>:
                    <select class = "minimal" style={props.modal.props.config.css.input} value = {this.minutesCurrentIndex} onChange={this.changeMinuteSelect}>
                        {this.state.minutes.map((k, index)=>{
                            return <option value={index}  >{k.toString().replace( /^([0-9])$/, '0$1' )}</option>
                        })}
                    </select>
                </div>
                {this.state.invalidNumber ? invalidMessage : null}
                {this.state.errorMessage ? errorMessage : null}
                <div class="call-me-btn-group">
                    <input class="call-me-number" style={props.modal.props.config.css.input} type="text" value = {this.state.number} onInput={this.changeNumber} onkeyup={(e)=> {if(e.keyCode==13)this.deferredClick()}}/>
                    <button class="call-me-btn" style={props.modal.props.config.css.callMeBtn} onClick={this.deferredClick}>{props.lang.btnCallDeferred}</button>
                </div>
            </div>
        )
    }
}

export default DeferredCall;


