
"use strict";

import { h, Component } from 'preact';
import Logger from '../../log';

class ShowDeferred extends Component {

    constructor(props){
        super(props);
        Logger.setLog(props.lang.forLog.showDeferred);
    }
    render(props) {
        return (
            <div>
                <div class="call-me-hook">
                    {props.lang.showDeferredTitle}
                </div>
                <br></br>
                <div class="show-deferred-text">
                    {props.lang.showDeferredText.replace("#time#", props.time).replace("#date#", props.date)}
                </div>
            </div>
        )
    }
}

export default ShowDeferred;


