import { h, render, Component } from 'preact';

import App from './app';
import './index.css'


let renderTo;

if (!renderTo) {
    renderTo = document.body
}

window.WebitelCallbackInit = function (config) {
    if(!config) return;
    render(
        <div id="callMeContent">
            <App config={config} />
        </div>,
        renderTo
    );
};

/*window.WebitelCallbackId  = '595f99c5743bc11a86708a75';
window.WebitelCallbackHost   = 'http://10.10.10.25/clickToCall';
window.WebitelCallbackDomain   = '10.10.10.144';*/
if(WebitelCallbackHost&&WebitelCallbackDomain&&WebitelCallbackId){
    var lt = document.createElement('script');
    lt.type ='text/javascript';
    lt.charset = 'utf-8';
    lt.async = true;
    lt.src = WebitelCallbackHost + '/domains/' + WebitelCallbackDomain + '/' + WebitelCallbackId + '.js';
    var sc = document.getElementsByTagName('script')[0];
    if (sc) sc.parentNode.insertBefore(lt, sc);
    else document.documentElement.firstChild.appendChild(lt);
}





