"use strict";

import { h, Component } from 'preact';

class Footer extends Component {
    constructor(props){
        super(props);
        this.getInitialState();
    }
    getInitialState () {
        this.setState({
            links: []
        });
    }
    render(props) {
        let items = null;

        if (props.items && props.items.length > 0) {
            items = props.items;
        } else {
            items = this.state.links;
        }
        
        return (
            <div class="call-me-card__actions call-me-card--border call-me-link" style={props.css}>
                {items}
            </div>
        )
    }
}

export default Footer;


