
"use strict";

import { h, Component } from 'preact';
import Logger from '../../log';

class HookWaiting extends Component {
    constructor(props)  {
         super(props);
         Logger.setLog(props.lang.forLog.hookWaiting);
         this.setState({
             time: props.modal.props.config.hookCountDown
         });
    }

    render(props) {
        let errorMessage = (<div class="invalid-message" style={props.modal.props.config.css.errorMessage} visible>
            {props.modal.state.errorMessage && props.modal.state.errorMessage.startsWith("-ERR") ? props.lang.serverErrorMessage : props.modal.state.errorMessage}
        </div>);
        this.timer = setTimeout(()=>{
            this.setState({
                time: this.state.time-1
            });
        }, 1000);
        if(this.state.time == 0||this.props.modal.state.callback_response){
            clearTimeout(this.timer);
        }
        return (
            <div>
                <div class="call-me-hook">
                    {props.lang.hookWaitingTitle}
                </div>
                <div class="hook-waiting">
                    {props.modal.state.errorMessage ? errorMessage : this.state.time}
                </div>
            </div>
        )
    }
}

export default HookWaiting;


