"use strict";

import { h, Component } from 'preact';
import Logger from '../../log';

class BtnCallMe extends Component {
    constructor(props) {
        super(props);
        Logger.setLog(props.lang.forLog.buttonShow);
    }

    render(props) {
        return (
            <div className="call-me-menu-button" style={props.css.buttonPosition} onClick={e => props.showModal()} >
                <div className="call-me-menu-main" style = {props.css.buttonColor}></div>
                <div className="call-me-menu-circle" style = {props.css.buttonCircle}></div>
                <div className="call-me-menu-circle-2"></div>
                <div className="call-me-menu-img"></div>
            </div>
        )
    }
}

export default BtnCallMe;



