"use strict";

import { h, Component } from 'preact';

class Timer extends Component {

    constructor (props) {
        super(props);
        this.timeStart = null;
    }
    
    getInitialState() {
        return { elapsed: 0 };
    }

    start(time) {
        this.timeStart = time;
        this.timer = setInterval(e => this.tick(), 50);
    }
    stop() {
        clearInterval(this.timer);
    }

    componentWillUnmount () {
        this.stop();
    }

    tick () {
        this.setState({elapsed: (new Date() - this.timeStart) / 1000});
    }

    render(props) {
        if (!this.timeStart)
            return;
        var hours   = Math.floor(this.state.elapsed / 3600 );
        var minutes = Math.floor(this.state.elapsed % 3600 / 60);
        var seconds = (this.state.elapsed % 60).toFixed(0);

        return <div className={props.class}>{toString(hours)}:{toString(minutes)}:{toString(seconds)}</div>;
    }
}

export default Timer;

function toString(time) {
    return time < 10 ? '0' + time : '' + time
}


