"use strict";

import { h, Component } from 'preact';
import ajax from '../../lib/ajax';
import Logger from '../../log';

class Hook extends Component {
    constructor(props){
        super(props);
        this.log = this.props.modal.props.lang.forLog;
        Logger.setLog(this.log.workPageOpen);
        this.changeNumber = this.changeNumber.bind(this);
        this.call = this.call.bind(this);
        this.setState({number: ""});
    }
    call(){
        if(this.props.modal.props.config.webRtcOnly){
            this.props.modal.tryCheckWebRtc();
            return;
        }
        let regex = this.props.modal.numberRegex;
        let isValidate = false;
        if(regex.length>0){
            for(let i = 0; i<regex.length; i++){
                if(regex[i].test(this.state.number)){
                    isValidate = true;
                    break;
                }
            }
        }
        else isValidate = true;
        if(isValidate&&this.state.number!=""){
            Logger.setLog(this.log.callButtonClick +" "+this.state.number);
            this.props.modal.changeView("waiting");
            let jobject = {};
            jobject.href = window.location.href;
            jobject.user_agent = navigator.userAgent;
            jobject.number = this.state.number;
            jobject.location = this.props.modal.props.location;
            jobject.logs = Logger.getData();
            ajax(this.props.modal.props.config.publicPostApi+"?domain="+WebitelCallbackDomain+"&widget="+WebitelCallbackId, (response) => {
                const res = JSON.parse(response);
                if(res.status === "error"){
                    Logger.setLog("Server error: "+res.info);
                    this.props.modal.callbackError(res);
                }
                else{
                    this.props.modal.callbackResponse();
                }
                console.log(res);
            }, jobject);
        }
        else{
            Logger.setLog(this.log.invalidNumber);
            this.setState({
                invalidNumber: true
            })
        }
    }

    changeNumber (event){
        let number =  event.target.value.replace(/[^0-9]/g,'');
        this.setState({
            number: number,
            invalidNumber: false
        });
    }

    render(props) {
        let invalidMessage = (<div class="invalid-number" style={props.modal.props.config.css.errorMessage} visible>
            {props.lang.invalidNumberMessage}
        </div>);
        return (
            <div>
                <div class="call-me-hook">
                {props.lang.hookTitle}
                </div>
                <div class="call-me-hook-text">
                    {props.lang.hookText.replace("#countdown#", props.modal.props.config.hookCountDown)}
                </div>
                {this.state.invalidNumber ? invalidMessage : null}
                <div class="call-me-btn-group" style={props.modal.props.config.webRtcOnly ? {margin: 'auto', width: '50%',  marginTop: '10px'} : ''}>
                    { !props.modal.props.config.webRtcOnly ? (<input class="call-me-number" style={props.modal.props.config.css.input} type="text" value = {this.state.number} onInput={this.changeNumber} onkeyup={(e)=> {if(e.keyCode==13)this.call()}}/>) : null}
                    <button class="call-me-btn" style={props.modal.props.config.webRtcOnly ? Object.assign({}, props.modal.props.config.css.callMeBtn,{width: '100%'}) : props.modal.props.config.css.callMeBtn} onClick={this.call}>{props.lang.btnCallWork}</button>
                </div>
            </div>
        )
    }
}

export default Hook;


