"use strict";

import { h, Component } from 'preact';
import WebRtc from '../webRtc';
import Home from '../home';
import DeferredCall from '../deferredCall';
import Footer from '../footer';
import HookWaiting from '../hookWaiting';
import ShowDeferred from '../showDeferred';
import Logger from '../../log';

class Modal extends Component {
    constructor (props) {
        super(props);
        this.log = props.lang.forLog;
        this.webRtcSupport = false;
        this.stream = null;
        this.component = null;
        this.webrtc = null;
        this.numberRegex = [];
        Logger.setLog(this.log.buttonClick);
        this.getInitialState();
        this.checkSupportWebRtc();
        //this.setRegex();
    }

    getInitialState() {
        this.setState({
            //numberRegex: [],
            visible: false,
            viewName: this.props.modalView,
            cancel_title: this.props.cancel_title ? this.props.cancel_title : 'Close',
            action_title: this.props.action_title ? this.props.action_title : 'OK',
            title: '',
            text: '',
            footer: []
        });
    }

    setRegex(){
        this.props.config.validateNumbers.map(item => { this.numberRegex.push(new RegExp(item));});
    }

    checkSupportWebRtc () {
        if(!this.props.config.useWebRtc) return;
        if(location.protocol != "https:") return;
        let prefix;
        let version;
        if (window.mozRTCPeerConnection || navigator.mozGetUserMedia) {
            prefix = 'moz';
            version = parseInt(navigator.userAgent.match(/Firefox\/([0-9]+)\./)[1], 10);
        } else if (window.webkitRTCPeerConnection || navigator.webkitGetUserMedia) {
            prefix = 'webkit';
            version = navigator.userAgent.match(/Chrom(e|ium)/) && parseInt(navigator.userAgent.match(/Chrom(e|ium)\/([0-9]+)\./)[2], 10);
        }
        let browserSupport = (prefix === 'moz' || prefix === 'webkit' && version > 41);
        if(!browserSupport) return;
        navigator.mediaDevices.enumerateDevices().then(devices => {
            for (let i = 0; i < devices.length; i++) {
                if (devices[i].kind === "audioinput") {
                    Logger.setLog(this.log.inputFound);
                    this.webRtcSupport = true;
                    break;
                }
            }
        });
    }

    open(text, title = '') {
        this.setState({
            visible: true,
            title: title,
            text: text
        });

    }

    close() {
        Logger.setLog(this.log.closeModal);
        let state = undefined;
        if(this.state.viewName == 'notWork' || this.state.viewName == 'showDeferredNotWork')
        {
            state = "notWork";
        }
        else
        {
            state = "work";
        }
        this.setState({
            visible: false,
            callback_response:false,
            errorMessage:false,
            viewName: state
        });
    }
    
    changeView (viewName) {
        this.setState({
            viewName:viewName,
            callback_response:false,
            errorMessage:false
        })
    }

    callbackResponse() {
        if(!this.state.callback_response)
            this.setState({callback_response:true});
    }

    callbackError(res) {
        this.setState({
            errorMessage: res.info
        })
    }

    setFooter (footer) {
        this.setState({footer})
    }


    callWebRtc(cb) {
        navigator.getUserMedia = navigator.getUserMedia ||
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia;

        if (navigator.getUserMedia) {
            navigator.getUserMedia({audio: true},
                (stream) => {
                    cb(null, stream);
                },
                (err) => {
                    cb(err);
                    // this.setState({error: "The following error occured: " + err.name});
                }
            );
        } else {
            cb(new Error("getUserMedia not supported"));
        }
    }

    tryCheckWebRtc () {
        this.callWebRtc((err, stream) => {
            this.stream = stream;
            this.changeView('webrtc');
        })
    }
    changeViewToShowDeferred (date, time, worktime){
        this.date = date;
        this.time = time;
        this.setState({viewName: worktime ? "showDeferred" : "showDeferredNotWork"})
    }
    render(props) {
        if (!this.state.visible)
            return;
        this.numberRegex=[];
        this.setRegex();
        console.log(props);

        var modalClass = "call-me-dialog" + (this.state.visible ? " call-me-dialog-open" : " call-me-dialog-close");
        var modalStyles = null; //this.state.visible ? {display: "table"} : {};

        let body = null;

        const itemsFooter = [];

        if (this.state.viewName === 'webrtc') {
            body = (
                <WebRtc ref={w => {this.webrtc = w}} stream={this.stream} modal={this} footer={this.footer} lang={props.lang}/>
            );

        } else if (this.state.viewName === 'deferred'/*||(!this.webRtcSupport&&props.config.webRtcOnly&&this.state.viewName !== 'notWork')*/) {
            body = (
                <DeferredCall work={true}  modal={this} footer={this.footer} lang={props.lang}/>
            );
            //if(!(props.config.webRtcOnly==true&&this.webRtcSupport==false))
            itemsFooter.push((
                <a href="javascript:void(0)" style={props.config.css.footer} onClick={ e => this.changeView('work')}>{props.lang.workLinkName}</a>
            ));
        } else {
            // TODO check work
            if(this.state.viewName === 'work'){
                if(!this.webRtcSupport&&props.config.webRtcOnly)
                    body = (
                        <DeferredCall work={true}  modal={this} footer={this.footer} lang={props.lang}/>
                    );
                else{
                    body = (
                        <Home  modal={this} footer={this.footer} lang={props.lang}/>
                    );

                    if (this.webRtcSupport&&!props.config.webRtcOnly) {
                        itemsFooter.push((
                            <a href="javascript:void(0)" style={props.config.css.footer} onClick={ e => this.tryCheckWebRtc()}>{props.lang.webRtcLinkName}</a>
                        ))
                    }

                    itemsFooter.push((
                        <a href="javascript:void(0)" style={props.config.css.footer} onClick={ e => this.changeView('deferred')}>{props.lang.deferredLinkName}</a>
                    ));
                }
            }
            else {
                if(this.state.viewName === 'waiting'){
                    body = (
                        <HookWaiting modal={this} footer={this.footer} lang={props.lang}/>
                    );
                    itemsFooter.push((
                        <a href="javascript:void(0)" style={props.config.css.footer} onClick={ e => this.changeView('work')}>{props.lang.workLinkName}</a>
                    ));
                }
                else{
                    if(this.state.viewName === 'showDeferred' || this.state.viewName === 'showDeferredNotWork' ){
                        body = (
                            <ShowDeferred modal={this} footer={this.footer} lang={props.lang} date = {this.date} time = {this.time}/>
                        );
                    }
                    else{
                        body = (
                            <DeferredCall work={false}  modal={this} footer={this.footer} lang={props.lang}/>
                        );
                    }
                }
            }
        }

        return (
            <div className={modalClass} style={modalStyles}>
                <div class="call-me-dialog-overlay"></div>

                <div class="call-me-dialog-content">

                    <div class="call-me-modal-content">
                        <div class="call-me-card call-me-shadow--2dp call-me-card--horizontal" style={props.config.css.modal}>
                            <div class="call-me-card__media" style={props.config.css.leftPanelColor}>

                            </div>
                            <div class="call-me-card__supporting-text" style={props.config.css.supportingText}>
                                {body}
                            </div>

                            <Footer items={itemsFooter} css = {props.config.css.footer} ref={f => this.footer = f} />

                            <div class="call-me-card__menu">
                                <a class="call-me-close-dialog" onClick={e => this.close(e)}>
                                    <svg viewBox="0 0 20 20">
                                        <path fill="rgba(0, 0, 0, 0.6);" d="M15.898,4.045c-0.271-0.272-0.713-0.272-0.986,0l-4.71,4.711L5.493,4.045c-0.272-0.272-0.714-0.272-0.986,0s-0.272,0.714,0,0.986l4.709,4.711l-4.71,4.711c-0.272,0.271-0.272,0.713,0,0.986c0.136,0.136,0.314,0.203,0.492,0.203c0.179,0,0.357-0.067,0.493-0.203l4.711-4.711l4.71,4.711c0.137,0.136,0.314,0.203,0.494,0.203c0.178,0,0.355-0.067,0.492-0.203c0.273-0.273,0.273-0.715,0-0.986l-4.711-4.711l4.711-4.711C16.172,4.759,16.172,4.317,15.898,4.045z"></path>
                                    </svg>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Modal;


