"use strict";

import { h, Component } from 'preact';
import Verto from '../../lib/verto';
import Timer from '../timer';
import Logger from '../../log';

class WebRtc extends Component {
    constructor (props) {
        super(props);
        this.log = props.modal.props.lang.forLog;
        Logger.setLog(this.log.openWebRtc);
        this.call = null;
        this.stream = null;
        this.timer = null;
        this.verto = null;
        this.modal = props.modal;
        this.getInitialState();
    }
    getInitialState() {
        this.setState({
            isHold: false,
            callParams: null,
            callState: "Connecting...",
            error: null
        });
    }

    callWebRtc() {
        navigator.getUserMedia = navigator.getUserMedia ||
            navigator.webkitGetUserMedia ||
            navigator.mozGetUserMedia;

        if (navigator.getUserMedia) {
            navigator.getUserMedia({audio: true, video: true},
                (stream) => {
                    if (typeof stream.stop === 'function') {
                        stream.stop();
                    } else {
                        if (stream.active){
                            var tracks = stream.getTracks();
                            tracks.forEach(function(track){
                                track.stop();
                            })
                        }
                    }
                    this.makeCall();
                },
                (err) => {
                    this.setState({error: "The following error occured: " + err.name});
                }
            );
        } else {
            this.setState({error: "getUserMedia not supported"});
        }
    }

    makeCall (stream) {
        var videoEl = document.createElement('video');
        videoEl.id = 'callMeWebRtc';
        videoEl.style.display = 'none';
        document.body.appendChild(videoEl);

        this.stream = stream;

        var cb = {
            onWSLogin: (e, success) => {
                console.error(e, success);

                setTimeout(() => {
                    if (this.call)
                        return;

                    this.call = this.verto.newCall({
                        destination_number: this.modal.props.config.destinationNumber, //Config
                        caller_id_name: "callMe",
                        caller_id_number: "callMe",
                        callStream: this.stream,
                        // useVideo: true,
                        userVariables: {
                            location: window.location.href
                        }
                    });
                }, 400);

                this.setState({callState: this.call.state.name, callParams: this.call.params});
            },
            onError: (e) => {
                debugger;
                console.error(e);
            },
            onDialogState: (d) => {
                console.warn(d);
                if (d.state === Verto.enum.state.destroy) {
                    this.timer.stop();
                    setTimeout(() => {
                        this.closeVerto();
                    }, 10);
                    this.call = null;
                    try {
                        document.body.removeChild(videoEl);
                    } catch (e) {}

                } else if (d.state === Verto.enum.state.active && !d.params.answerTime) {
                    d.params.answerTime = Date.now();
                    this.timer.start(d.params.answerTime);
                } else if (d.state === Verto.enum.state.recovering) {
                    this.call = d;

                    this.modal.callWebRtc((err, callStream) => {
                        if (err) {
                            // TODO
                        }

                        this.call.answer({callStream});
                    })
                }

                this.setState({callState: d.state.name, callParams: d.params});
            },
            onChangeDisplay: d => {
                this.setState({callParams: d.params});
            }
        };
        this.verto = new Verto({
            login: window.location.host+"@"+WebitelCallbackDomain,
            passwd: "xxx",
            socketUrl: this.props.modal.props.config.publicWebRtc,
            tag: () => {
                return videoEl.id;
            }
        }, cb);
    }

    cancelCall () {
        Logger.setLog(this.log.webRtcButtonClick + " " + this.props.modal.props.lang.cancelWebrtc);
        this.closeVerto();
        this.modal.changeView('work')
    }

    closeVerto () {
        if (this.verto) {
            this.verto.logout();
            this.verto = null;
        }
        if (this.stream) {
            if(typeof this.stream.stop === 'function') {
                this.stream.stop();
            } else if (this.stream.active) {
                this.stream.getTracks().forEach(track => track.stop())
            }
            this.stream = null;
        }
    }

    getFooter (modal) {

        if (this.call && this.state.callParams) {
            return [
                <a href="javascript:void(0)" style={modal.props.config.css.footer} onClick={ e => {
                    this.call.hangup();
                    Logger.setLog(this.log.webRtcButtonClick + " " + modal.props.lang.hangup);
                }}>{modal.props.lang.hangup}</a>,
                //<a href="javascript:void(0)" onClick={ e => this.call.toggleHold()}>{modal.props.lang.hold}</a>
                <a href="javascript:void(0)" style={modal.props.config.css.footer} onClick={ e => {
                    this.state.isHold = !this.state.isHold;
                    this.call.toggleHold();
                    Logger.setLog(this.log.webRtcButtonClick + " "+this.state.isHold ? modal.props.lang.hold : modal.props.lang.unhold)
                }}>{this.state.isHold ? modal.props.lang.unhold : modal.props.lang.hold}</a>
            ]
        } else if (!this.call && this.state.callParams) {
            return [
                <a href="javascript:void(0)" style={modal.props.config.css.footer} onClick={ e => {
                    modal.changeView('work');
                    Logger.setLog(this.log.webRtcButtonClick + " "+modal.props.lang.returnFromWebrtc);
                }}>{modal.props.lang.returnFromWebrtc}</a>
            ];
        } else {

            return [
                <a href="javascript:void(0)" style={modal.props.config.css.footer} onClick={ e => this.cancelCall()}>{modal.props.lang.cancelWebrtc}</a>
            ];
        }
    }

    render(props) {
        let body = [];
        if (this.state.error) {
            body.push((
                <div>Error: {this.state.error}</div>
            ))
        } else {

            body.push((
                <h3>State: {this.state.callState}</h3>
            ));

            if (this.state.callParams) {
                body.push((
                    <h4>{this.state.callParams.remote_caller_id_name}({this.state.callParams.remote_caller_id_number}) </h4>
                ))
            }
        }
        this.modal = props.modal;
        props.footer.setState({links: this.getFooter(this.modal)});
        if (!this.state.callParams) {
            this.makeCall(props.stream);
        }
        return (
            <div class="call-me-webrtc-container">
                <div class="ca-name">{this.state.callParams && this.state.callParams.remote_caller_id_name}</div>
                <div class="ca-number">{this.state.callParams && this.state.callParams.remote_caller_id_number}</div>
                <Timer class="ca-number" ref={ t => this.timer = t} />
                <div class="ca-status">{(this.state.callState || "").toUpperCase()}</div>
            </div>
        )
    }
}

export default WebRtc;


